import React from "react";
import PropTypes from "prop-types";
import { graphql } from "gatsby";
import { Row, Col, Block } from "jsxstyle";
import { Helmet } from "react-helmet";
import SEO from "../components/seo";
import Layout from "../components/layout";
import TinyPost from "../components/tinyPost";
import Navigation from "../components/navigation";

const Projects = ({ data, pageContext: { nextPagePath, previousPagePath } }) => {
  const {
    allMarkdownRemark: { edges: posts },
  } = data;

  const sortedPosts = posts.sort((post1, post2) => {
    const postOrder1 = post1.node.frontmatter.order;
    const postOrder2 = post2.node.frontmatter.order;

    if (postOrder1 > postOrder2) {
      return 1;
    } else {
      return -1;
    }
  }, posts);

  return (
    <>
      <Col width="100%" boxSizing="content-box" alignItems="center">
        <SEO />
        <Layout path="/projects">
          <Row
            fontStyle="italic"
            color="rgba(0, 0, 0, 0.4)"
            marginTop="32px"
            marginBottom="16px"
          >
            Scroll down to view selected projects.
          </Row>
          <Col>
            {sortedPosts.map(({ node }, index) => {
              const {
                id,
                excerpt: autoExcerpt,
                frontmatter: {
                  title,
                  date,
                  path,
                  author,
                  coverImage,
                  coverVideo,
                  links,
                  excerpt,
                  tags,
                  readMoreButton,
                },
              } = node;

              return (
                <div className={"index" + index} key={"index" + index}>
                  <TinyPost
                    title={title}
                    date={date}
                    path={path}
                    author={author}
                    coverImage={coverImage}
                    coverVideo={coverVideo}
                    links={links}
                    tags={tags}
                    readMoreButton={readMoreButton}
                    excerpt={excerpt}
                  />
                </div>
              );
            })}
          </Col>
          <Navigation
            previousPath={previousPagePath}
            previousLabel="Newer posts"
            nextPath={nextPagePath}
            nextLabel="Older posts"
          />
        </Layout>
      </Col>
    </>
  );
};

//sort: { fields: [frontmatter___date], order: DESC }
export const postsQuery = graphql`
  query($limit: Int!, $skip: Int!) {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "//posts//" } }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          id
          excerpt
          frontmatter {
            title
            date
            path
            author
            excerpt
            tags
            coverImage {
              publicURL
            }
            coverVideo {
              publicURL
            }
            links
            readMoreButton
            order
          }
        }
      }
    }
  }
`;

export default Projects;
